import React, { Component } from 'react';
import { Table, Button, Modal } from 'antd';
import { setStudentForTranscript } from 'dump/data';
import TableTranscriptEachStudent from './TableTranscriptEachStudent';

class TableListStudent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: false,
    };

    this.columns = [
      {
        title: 'M sinh vin',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'H v tn',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: () => (
          <Button type="primary" onClick={this.showModal}>
            Xem
          </Button>
        ),
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }

  showModal = () => {
    this.setState({
      visible: true,
    });
  };
  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };

  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };

  render() {
    return (
      <div>
        <Table
          bordered
          rowSelection={this.rowSelection}
          dataSource={setStudentForTranscript}
          columns={this.columns}
        />
        ;
        <Modal
          title="Bng im"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
        >
          <TableTranscriptEachStudent />
        </Modal>
      </div>
    );
  }
}

export default TableListStudent;



