import React, { Component } from 'react';
import { Row, Col, Form, Radio, Select, Input, InputNumber, Checkbox } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { schoolYearsTime, formatTranscriptForm, typeTranscriptForm } from 'dump/data';
import './style.scss';

@Form.create({ name: 'formSerchIndividual' })
class FormInfoStudent extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const RadioGroup = Radio.Group;
    const { Option } = Select;
    return (
      <Form onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
          <Col span={8}>
            <Form.Item label="Mu ton kha:">
              {getFieldDecorator('formatFullCourse', {})(
                <Select>
                  {formatTranscriptForm.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Hc k:">
              <InputNumber min={1} max={3} defaultValue={1} style={{ width: '100%' }} />
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Nm hc:">
              {getFieldDecorator('schoolYearsTime', {})(
                <Select>
                  {schoolYearsTime.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Bng im:">
              {getFieldDecorator('typeTranscript', {})(
                <Select>
                  {typeTranscriptForm.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={24}>
          <Col span={12}>
            <Form.Item>
              {getFieldDecorator('conCerti', {})(
                <RadioGroup onChange={this.onChange} value={this.state.value}>
                  <Radio value={1}>Bng im ngnh chnh</Radio>
                  <Radio value={2}>Bng im Ngnh 2</Radio>
                </RadioGroup>
              )}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item>
              {getFieldDecorator('studentDropoutOfSchool', {})(
                <div>
                  <Checkbox> Hin th sv ang thi hc</Checkbox>
                  <Checkbox> In trc tip</Checkbox>
                  <Input placeholder="Nhp m, tn SV" />
                </div>
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={24} className="labelText">
          <Col span={8}>
            <div className="titleText">
              <span>TBCHT(4):</span> <b>3.40</b>
            </div>
          </Col>
          <Col span={8}>
            <div className="titleText">
              <span>TBCHT(10):</span> <b>8.60</b>
            </div>
          </Col>
          <Col span={8}>
            <div className="titleText">
              <span>TBCHT tch ly:</span> <b>3.40</b>
            </div>
          </Col>
        </Row>
        <Row gutter={24} className="labelText">
          <Col span={8}>
            <div className="titleText">
              <span>S tn ch  tch ly:</span> <b>10</b>
            </div>
          </Col>
          <Col span={8}>
            <div className="titleText">
              <span>Xp loi hc tp</span> <b>Gii</b>
            </div>
          </Col>
          <Col span={8}>
            <div className="titleText">
              <span>Xp hng hc tp: </span> <b>5</b>
            </div>
          </Col>
        </Row>
        <Row gutter={24} className="labelText">
          <Col span={8}>
            <div className="titleText">
              <span>S mn hc li: </span> <b>0</b>
            </div>
          </Col>
          <Col span={8}>
            <div className="titleText">
              <span>S mn ch im: </span> <b>0</b>
            </div>
          </Col>
          <Col span={8}>
            <div className="titleText">
              <span>S mn thi li: </span> <b>0</b>
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormInfoStudent;



