import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Divider, Button, Icon } from 'antd';
import { BtnClear, BtnSearch } from 'components/common/button';
import FormInfoStudentForTranscript from './FormInfoStudentForTranscript';
import TableListStudent from './TableListStudent';
import MenuTabTop from './MenuTabTop';
import './style.scss';

class ListStudentForTranscriptPage extends Component {
  state = {};
  render() {
    return (
      <Card title="In bng im sinh vin">
        <Row className="rowMargin">
          <Col span={24}>
            <MenuTabTop />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <FormInfoStudentForTranscript />
          </Col>
        </Row>
        <Divider className="rowMargin">Danh sch sinh vin cn in bng im</Divider>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <Button className="btnPrintTranscript">
                <Icon type="printer" /> In bng im
              </Button>
              <BtnSearch />
              <BtnClear />
            </div>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableListStudent />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListStudentForTranscriptPage />
  </Page>
);



